 .header{
     display: flex;
     justify-content: space-between;
     flex: 1;
     padding: 12px;
     position: relative;
     align-items: center;
 }

 .menu{
     display: flex;
     flex: 1;
     justify-content: flex-end;
 }

 .menu-icon{
     font-size: 26px;
 }

 .mobile-menu{
     display: none;
 }

 .wen-menu{
     display: flex;
 }

 .logo{
     display: flex;
     font-size: 36px;
     color: rgb(183, 250, 0);
     font-weight: 800;
 }

 @media only screen and (max-width:720px){
     .mobile-menu{
         display: flex;
     }

     .web-menu{
         display: none;
     }

     .logo{
         font-size: 24px;
     }
 }