.about{
    padding-bottom: 48px;
}

.about-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.picture{
    height: 420px;
}

.about-info{
    font-size: 24px;
}

.info-name{
    color: rgb(255, 0, 21);
    font-size: 32px;
    font-weight: 800;
}

@media only screen and (max-width:720px){
    .about-top{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .picture{
        height: 280px;
    }

    .about-info{
        font-size: 20px;
    }

    .info-name{
        font-size: 24px;
    }
}